<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('cheweixinxi','首页总数')">
				<el-card v-show="countTypeList.closecheweixinxiCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								车位信息
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddencheweixinxiCountType')" class="showIcons"
									:class="countTypeList.hiddencheweixinxiCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closecheweixinxiCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddencheweixinxiCountType">
							<div class="count_title">车位信息总数</div>
							<div class="count_num">{{cheweixinxiCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('chelianglichang','首页总数')">
				<el-card v-show="countTypeList.closechelianglichangCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								车辆离场
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenchelianglichangCountType')" class="showIcons"
									:class="countTypeList.hiddenchelianglichangCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closechelianglichangCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenchelianglichangCountType">
							<div class="count_title">车辆离场总数</div>
							<div class="count_num">{{chelianglichangCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('cheweixinxi','首页统计')">
				<el-card v-show="cardTypeList.closecheweixinxiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								车位信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddencheweixinxiChartType1')" class="showIcons"
									:class="cardTypeList.hiddencheweixinxiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closecheweixinxiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddencheweixinxiChartType1">
							<div id="cheweixinxicheweileixingEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('chelianglichang','首页统计')">
				<el-card v-show="cardTypeList.closechelianglichangChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								车辆离场
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenchelianglichangChartType1')" class="showIcons"
									:class="cardTypeList.hiddenchelianglichangChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closechelianglichangChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenchelianglichangChartType1">
							<div id="chelianglichangtingchefeiEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closecheweixinxiCountType = true
		countTypeList.value.hiddencheweixinxiCountType = true
		if(btnAuth('cheweixinxi','首页总数')){
			getcheweixinxiCount()
		}
		countTypeList.value.closechelianglichangCountType = true
		countTypeList.value.hiddenchelianglichangCountType = true
		if(btnAuth('chelianglichang','首页总数')){
			getchelianglichangCount()
		}
	}
	const cheweixinxiCount = ref(0)
	const getcheweixinxiCount = () => {
		context?.$http({
			url:'cheweixinxi/count',
			method: 'get'
		}).then(res=>{
			cheweixinxiCount.value = res.data.data
		})
	}
	const chelianglichangCount = ref(0)
	const getchelianglichangCount = () => {
		context?.$http({
			url:'chelianglichang/count',
			method: 'get'
		}).then(res=>{
			chelianglichangCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closecheweixinxiChartType1: true,
		hiddencheweixinxiChartType1: true,
		closechelianglichangChartType1: true,
		hiddenchelianglichangChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('cheweixinxi','首页统计')){
			getcheweixinxiChart1()
		}
		if(btnAuth('chelianglichang','首页统计')){
			getchelianglichangChart1()
		}
	}
	const getcheweixinxiChart1 = () => {
		nextTick(()=>{
			var cheweileixingEchart1 = echarts.init(document.getElementById("cheweixinxicheweileixingEchart1"),'macarons');
			context?.$http({
				url: "cheweixinxi/group/cheweileixing",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].cheweileixing);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].cheweileixing
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '车位类型统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				cheweileixingEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				cheweileixingEchart1.setOption(option);
				//根据窗口的大小变动图表
				cheweileixingEchart1.resize();
			})
		})
	}
	const getchelianglichangChart1 = () => {
		nextTick(()=>{
			var tingchefeiEchart1 = echarts.init(document.getElementById("chelianglichangtingchefeiEchart1"),'macarons');
			context?.$http({
				url: `chelianglichang/value/lichangshijian/tingchefei`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].lichangshijian);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].lichangshijian
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '停车收入统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				tingchefeiEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				tingchefeiEchart1.setOption(option);
				//根据窗口的大小变动图表
				tingchefeiEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: 50%;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 1px solid #e4e7ed;
			box-shadow: 0px 0px 12px rgba(0,0,0,.12);
			margin: 0 10px 10px;
			flex: 1;
			width: 100%;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				width: 100%;
				border-width: 0 0 1px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #f00;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #aaa;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px;
					text-align: center;
					// 总数标题
					.count_title{
						color: #666;
						font-size: 16px;
						line-height: 2;
					}
					// 总数数字
					.count_num{
						color: #F00;
						font-size: 30px;
						line-height: 2;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 20px;
		background: url(http://clfile.zggen.cn/20240203/09a98f4852104a599055d47953ef0a23.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 1px solid #e4e7ed;
			box-shadow: 0px 0px 12px rgba(0,0,0,.12);
			margin: 0 10px 10px;
			flex: 1;
			width: 100%;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				width: 100%;
				border-width: 0 0 1px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #f00;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #aaa;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
